import React, { useEffect, useRef, useState } from 'react';
import './Header.css';

const Header: React.FC = () => {
    const [isFixedTop, setIsFixedTop] = useState(false);
    const headerRef = useRef<HTMLElement | null>(null);
    const [headerHeight, setHeaderHeight] = useState(0);

    useEffect(() => {
        // Headerì˜ ë†’ì´ë¥¼ ì¸¡ì •
        if (headerRef.current) {
            setHeaderHeight(headerRef.current.offsetHeight);
        }

        function handleScroll() {
            const introSection = document.getElementById('intro');
            if (!introSection) return;

            // ì¸íŠ¸ë¡œ ì„¹ì…˜ì˜ ë°”ë‹¥ ìœ„ì¹˜
            const introRect = introSection.getBoundingClientRect();
            const introBottom = introRect.bottom + window.scrollY;

            // í˜„ì¬ ìŠ¤í¬ë¡¤ ìœ„ì¹˜
            const scrollTop = window.scrollY || document.documentElement.scrollTop;

            // Headerì˜ ë†’ì´ë¥¼ ê³ ë ¤í•˜ì—¬ ê³ ì • ì‹œì  ì¡°ì •
            if (scrollTop >= (introBottom - headerHeight)) {
                setIsFixedTop(true);
            } else {
                setIsFixedTop(false);
            }
        }

        window.addEventListener('scroll', handleScroll);
        handleScroll(); // ì´ˆê¸° ìŠ¤í¬ë¡¤ ìœ„ì¹˜ì— ë”°ë¼ ìƒíƒœ ì„¤ì •

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, [headerHeight]);

    return (
        <header ref={headerRef} className={`main-header ${isFixedTop ? 'fixed-top' : ''}`}>
            <div className="header-inner">
                {/* --- ì—¬ê¸°ì„œ "ã…‡ã…‡ã…" ë¡œê³ ë¥¼ SVG íŒŒì¼ë¡œ ì‚½ì… --- */}
                <div className="logo">
                    <img
                        src={process.env.PUBLIC_URL + '/assets/IlilmanLogo.svg'}
                        alt="ã…‡ã…‡ã… ë¡œê³ "
                        style={{ height: '40px' }}
                    />
                </div>

                <nav>
                    <ul>
                        <li>
                            <a href="#intro">HOME</a>
                        </li>
                        <li>
                            <a href="#about">ABOUT</a>
                        </li>
                        <li>
                            <a href="#routine">MOIM</a>
                        </li>
                        <li>
                            <a href="#faq">FAQ</a>
                        </li>
                        <li>
                            <a href="#contact-cta">CONTACT</a>
                        </li>
                    </ul>
                </nav>
            </div>
        </header>
    );
};

export default Header;
